
/*education outer-------------------------------------------*/
.education-content-box-outer{
	padding: 4vw 0vw 4vw 4.5vw;
}

/*education img box-----------------------------------------*/
.education-img-box.skills-img-box{
	background-image: url('../img/pEducation2.webp');
	background-size: 42vw 34vw;
}
.education-img-box.skills-img-box h1{
	font-size: 7.5vw;
}

/*education content---------------------------------------------*/
.education-content-box{
	margin: 2vw 0px;
	position: relative;
}
.education-content-box::before{
	content: "";
	position: absolute;
	height: 100%;
	width: .3vw;
	left: 0;
	top: 0;
	background-color: black;
}
.education-content-box:hover.education-content-box::before{
	background-color: #5e17eb;

}
.education-content-box:hover.education-content-box h1{
	color: #5e17eb;
}
.education-content-box span{
	color: black;
	font-family: 'Poppins',sans-serif;
}
.education-content-box h1{
	font-family: 'Poppins',sans-serif;
	font-size: 2.5vw;
	color: #5e17eb;
	color: black;
}
.education-content-box h4,h5{
	font-family: 'Poppins',sans-serif;
	font-weight: 400;
	font-size: 1.3vw;
	color: #5e17eb;

}
.education-content-box h5{
	font-family: 'Poppins',sans-serif;
	font-size: 1.1vw;
}


/*media query*********************************************************************/
/*extra small*/
@media screen and (max-width:576px){
/*	eduation outer-------------------------------*/
	.education-content-box-outer{
		padding: 6vw 0vw 6vw 4vw;
	}
/*		education img box-----------------------*/
	.education-img-box.skills-img-box{
		height: 170vw;
		background-size: 100% 82vw;
		padding: 6vw 0vw 6vw 5.5vw;
	}
	.education-img-box.skills-img-box h1{
		font-size: 14.5vw;
	}

	/*education content---------------------------------------------*/
	.education-content-box{
		margin: 3vw 0px;
	}
	.education-content-box::before{
		width: .5vw;
	}
	.education-content-box h1{
		font-size: 10vw;
	}
	.education-content-box h4,h5{
		font-size: 5vw;
	}
	.education-content-box h5{
		font-size: 4.5vw;
	}


}


/*small*/
@media screen and (min-width:577px) and (max-width:767px){
/*	eduation outer-------------------------------*/
	.education-content-box-outer{
		padding: 6vw 0vw 6vw 4vw;
	}
/*		education img box-----------------------*/
	.education-img-box.skills-img-box{
		height: 170vw;
		background-size: 100% 82vw;
		padding: 6vw 0vw 6vw 4.4vw;
	}
	.education-img-box.skills-img-box h1{
		font-size: 14.5vw;
	}

	/*education content---------------------------------------------*/
	.education-content-box{
		margin: 3vw 0px;
	}
	.education-content-box::before{
		width: .5vw;
	}
	.education-content-box h1{
		font-size: 10vw;
	}
	.education-content-box h4,h5{
		font-size: 5vw;
	}
	.education-content-box h5{
		font-size: 4.5vw;
	}

 
}


